* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.content-container {
  position: relative;
  min-height: 100vh;
}
.content-wrapper {
  padding-bottom: 2.5rem;
}

.footer-wrapper {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}
